import { createModel } from "@rematch/core";
import { RootModel } from ".";

const exchange1 = {
  categoryList: [
    {category: '', 
     value: [
      ['', 'adventures'],
      ['', 'fantasy']
    ]},
    {category: '', 
     value: [
      ['', 'pylit'],
    ]},
    {category: ' ', 
     value: [
      ['', 'chemistry'],
      ['', 'physics']
    ]},
  ]
}

const exchange2 = {
  categoryList: [
    {category: '', 
     value: [
      [' ', 'childbooks'],
      ['', 'fantasy']
    ]},
    {category: ' ', 
     value: [
      ['', 'esoterics']
    ]},
    {category: '', 
     value: [
      ['  ', 'goodshape'],
    ]},
  ]
}

const exchange3 = {
  categoryList: [
    {category: '', 
     value: [
      ['', 'psychology'],
    ]},
    {category: '', 
     value: [
      ['', 'gonkyr'],
    ]},
    {category: '', 
     value: [
      ['', 'filmed'],
    ]},
  ]
}

interface ICategoryListItem {
  category: string;
  value: string[][];
}

interface IBookListItem {
    data: {
      [key: string]: {
        categoryList: ICategoryListItem[]
      }
    }
}

interface IPayload {
  categoryList: ICategoryListItem[]
}

export const requestWishBooks = createModel<RootModel>()({
    state: {
      data: {
          book1: exchange1,
          book2: exchange2,
          book3: exchange3,
      }
    } as IBookListItem,

    reducers: {
      SET_REQUEST_DATA: (state: IBookListItem, payload: object) => {
        return {
          ...state,
          data: {...state.data, ...payload }
        }
      },
      ADD_REQUEST_DATA: (state: IBookListItem, payload: IPayload) => {
        return {
          ...state,
          data: {...state.data, [`book${Object.keys(state.data).length+1}`]: payload }
          //       
        }
      },
    },
});