import * as yup from "yup";
const texts = {
    pass: '   ',
    email: 'Email  ',
    name: '  ',
    secondName: '  ',
    city: '  ',
    index: '  ',
    nickname: '  ',
    street: '  ',
    home: '   ',
    authorName: '  ',
    authorSurname: '  ',
    book: '  ',
    year: '  ',
}

export const baseURL = ''


const emailMatches = /^(([a-z0-9_-]+\.)*[a-z0-9_-]+@[a-z0-9_-]+(\.[a-z0-9_-]+)*\.[a-z]{2,6}){0,1}$/

export const VALIDATION = {
    SIGN_IN: yup.object().shape({
        email: yup
        .string()
        .matches(emailMatches, "Email   ")
        .required(texts.email),
        password: yup.string().trim().max(64).required(texts.pass),
    }),
    SIGN_UP: yup.object().shape({
        email: yup
        .string()
        .matches(emailMatches, "Email   ")
        .required("Email  "),
        password: yup.string().trim().required(texts.pass).min(8, '  8 ').max(64).matches(/^((?=.*?[A-Z])(?=.*?[a-z]))|((?=.*?[-])(?=.*?[-]))(?=.*?[0-9])[a-zA-Z--\d]$/, '      '),
        confirmPassword: yup.string().trim().oneOf([yup.ref('password'), null], '  ').required('  '),
        name: yup.string().trim().max(25, '  25 ').required(texts.name).matches(/^[---]+$/, '  '),
        secondName: yup.string().trim().max(50, '  50 ').required(texts.secondName).matches(/^[---]+$/, '  '),
        thirdName: yup.string().trim().max(25, '  25 ').matches(/^[---]+$/, '  '),
        indexLocation: yup.string().trim().required(texts.index).matches(/^\d+$/, '  ').max(6, '  6 '),
        city: yup.string().trim().max(20, '  20 ').required(texts.city).matches(/^[---]+$/, '  '),
        nickname: yup.string().required(texts.nickname).matches(/^([a-zA-Z][a-zA-Z0-9-_\.]+)|([--][--0-9-_\.]+){1,50}$/, '   ').trim().max(50, '  50 '),
        street: yup.string().trim().max(25, '  25 ').required(texts.street).matches(/^[--\d- ]+$/, '    '),
        homeNumber: yup.string().trim().max(5, '  5 ').required(texts.home).matches(/\d+[-]{0,1}/i, '     '),
        buildNumber: yup.string().trim().max(3, '  3 ').matches(/[\d]{0,2}[-]{0,1}$/, '     '),
        flatNumber: yup.string().trim().max(3, '  3 ').matches(/[\d ]{0,3}$/, '  '),
        terms: yup.boolean().oneOf([true], '    ').required('  ')
    }),
    PERSONAL_DATA: yup.object().shape({
        email: yup
        .string()
        .matches(emailMatches, "Email   ")
        .required("Email  "),
        name: yup.string().trim().max(25, '  25 ').required(texts.name).matches(/^[---]+$/, '  '),
        secondName: yup.string().trim().max(50, '  50 ').required(texts.secondName).matches(/^[---]+$/, '  '),
        thirdName: yup.string().trim().max(25, '  25 ').matches(/^[---]+$/, '  '),
        indexLocation: yup.string().trim().required(texts.index).matches(/^\d+$/, '  ').max(6, '  6 '),
        city: yup.string().trim().max(20, '  20 ').required(texts.city).matches(/^[---]+$/, '  '),
        nickname: yup.string().required(texts.nickname).matches(/^([a-zA-Z][a-zA-Z0-9-_\.]+)|([--][--0-9-_\.]+){1,50}$/, '   ').trim().max(50, '  50 '),
        street: yup.string().trim().max(25, '  25 ').required(texts.street).matches(/^[--\d- ]+$/, '    '),
        homeNumber: yup.string().trim().max(5, '  5 ').required(texts.home).matches(/\d+[-]{0,1}/i, '     '),
        buildNumber: yup.string().trim().max(3, '  3 ').matches(/[\d]{0,2}[-]{0,1}$/, '     '),
        flatNumber: yup.string().trim().max(3, '  3 ').matches(/[\d ]{0,3}$/, '  '),
    }),
    RESET_PASS: yup.object().shape({
        email: yup
        .string()
        .matches(emailMatches, "Email   ")
        .required("Email  "),
    }),
    BOOK_INFO: yup.object().shape({
        authorName: yup.string().required(texts.authorName).matches(/^([a-zA-Z][a-zA-Z\.]+)|([--][--\.]+){1,20}$/, '   ').trim().max(20, '  20 '),
        authorSurname: yup.string().required(texts.authorSurname).matches(/^([a-zA-Z][a-zA-Z\.]+)|([--][--\.]+){1,50}$/, '   ').trim().max(50, '  50 '),
        book: yup.string().required(texts.book).matches(/^([a-zA-Z][a-zA-Z0-9-_\.]+)|([--][--0-9-_\.]+){1,50}$/, '   ').trim().max(50, '  50 '),
        isbn: yup.string().trim().matches(/^$|^(97[89]-\d{1,5}|\d{1,5})-\d{1,7}-\d{1,6}-\d$/, ' ').max(17, '  17 '),
        year: yup.string().trim().required(texts.year).matches(/(^1\d{3}|^20[01]\d|^202[01])/, ' ').max(4, '  4 '),
    }),
    DELIVERY_INFO: yup.object().shape({
        name: yup.string().trim().max(25, '  25 ').required(texts.name).matches(/^[---]+$/, '  '),
        secondName: yup.string().trim().max(50, '  50 ').required(texts.secondName).matches(/^[---]+$/, '  '),
        thirdName: yup.string().trim().max(25, '  25 ').matches(/^[---]+$/, '  '),
        indexLocation: yup.string().trim().required(texts.index).matches(/^\d+$/, '  ').max(6, '  6 '),
        city: yup.string().trim().max(20, '  20 ').required(texts.city).matches(/^[---]+$/, '  '),
        street: yup.string().trim().max(25, '  25 ').required(texts.street).matches(/^[--\d- ]+$/, '    '),
        homeNumber: yup.string().trim().max(5, '  5 ').required(texts.home).matches(/\d+[-]{0,1}/i, '     '),
        buildNumber: yup.string().trim().max(3, '  3 ').matches(/[\d]{0,2}[-]{0,1}$/, '     '),
        flatNumber: yup.string().trim().max(3, '  3 ').matches(/[\d ]{0,3}$/, '  '),
    }),
    TRACKING: yup.object().shape({
        track: yup.string().trim().max(15).required('  '),
    }),
    FEEDBACK: {
        AUTH: yup.object().shape({
            topic: yup.string().trim().max(100).required('   '),
            text: yup.string().trim().max(1024).required('     '),
        }),
        NOT_AUTH: yup.object().shape({
            topic: yup.string().trim().max(100).required('   '),
            text: yup.string().trim().max(1024).required('     '),
            name: yup.string().trim().max(64).required(texts.name),
            secondName: yup.string().trim().max(64).required(texts.secondName),
            email: yup
            .string()
            .matches(emailMatches, "Email   ")
            .required("Email  "),
            terms: yup.boolean().oneOf([true], '    ').required('  ')
        }),
        
    },
}   