import { createModel } from "@rematch/core";
import { RootModel } from ".";

export interface IMenuItem {
  title: string;
  link: string;
}

const list = [
     {
      title: "",
      link: "/"
     },
     {
      title: " ",
      link: "/start"
     },
     {
      title: " ",
      link: "/userChange/offer"
     },
     {
        title: " ",
        link: "/feedback"
     },
]

const loginModal = false

export const menu = createModel<RootModel>()({
  state: {
    error: null,
    list,
    loginModal,
  },
  reducers: {
    SET_MODAL: (state, payload: boolean) => {
      return {
        ...state,
        loginModal: payload
      }
    },
  },
  effects: (dispatch) => ({
  
  }),
});
